export let _YEARS: number[] = [];

for (let i = 1900; i <= 2023; i++)
    _YEARS.push(i);

export let _MONTHS: number[] = [];

for (let i = 1; i <= 12; i++)
    _MONTHS.push(i);

export let _DAYS: number[] = [];

for (let i = 1; i <= 31; i++)
    _DAYS.push(i);

export const MAX_STEPS = 3;